*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

body {
  min-height: 100vh;
  font-family: var(--font-inter);
  background-color: var(--col-bg);
  color: white;
  padding: 1.5625rem;
  max-width: 68.75rem;
  margin: 0 auto;
}

.u-mt-large {
  margin-top: 36px !important;
}

.u-mt-small {
  margin-top: 12px !important;
}

.u-mt-tiny {
  margin-top: 6px !important;
}

.u-text-subheading {
  font-size: 0.875rem;
  font-weight: 700 !important;
  color: var(--col-txt1) !important;
}

.u-divider {
  width: 100%;
  height: 1px;
  margin: 12px 0;
  background-color: var(--col-bg-card);
}
@media (min-width: 40em) {
  .u-divider {
    display: none;
  }
}

.span-all {
  grid-column: 1/-1;
}

:root {
  --color-mode: "light";
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --insta-start: hsl(37, 97%, 70%);
  --insta-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --bg-dark: hsl(230, 17%, 14%);
  --bg-top-dark: hsl(232, 19%, 15%);
  --bg-card-dark: hsl(228, 28%, 20%);
  --bg-card-hover-dark: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --bg-light: hsl(0, 0%, 100%);
  --bg-top-light: hsl(225, 100%, 98%);
  --bg-card-light: hsl(227, 47%, 96%);
  --bg-card-hover-light: hsl(227, 47%, 90%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --col-bg: var(--bg-light);
  --col-bg-top: var(--bg-top-light);
  --col-bg-card: var(--bg-card-light);
  --col-txt1: var(--light-text1);
  --col-txt2: var(--light-text2);
  --col-card-hover: var(--bg-card-hover-light);
}

html[data-theme=dark] {
  --color-mode: "dark";
  --col-bg: var(--bg-dark);
  --col-bg-top: var(--bg-top-dark);
  --col-bg-card: var(--bg-card-dark);
  --col-txt1: var(--dark-text1);
  --col-txt2: var(--dark-text2);
  --col-card-hover: var(--bg-card-hover-dark);
}

:root {
  --font-inter: "Inter", sans-serif;
}

body {
  font-family: var(--font-inter);
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  color: var(--col-txt2);
  font-size: 1.5rem;
}
@media (min-width: 56.25em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

@media (prefers-color-scheme: dark) {
  :root {
    --color-mode: "dark";
    --col-bg: var(--bg-dark);
    --col-bg-top: var(--bg-top-dark);
    --col-bg-card: var(--bg-card-dark);
    --col-txt1: var(--dark-text1);
    --col-txt2: var(--dark-text2);
    --col-card-hover: var(--bg-card-hover-dark);
  }
}
.header {
  display: flex;
  flex-direction: column;
}
@media (min-width: 40em) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__right {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 40em) {
  .header__right {
    align-items: center;
  }
}
.header__toggle {
  margin-left: 0.75rem;
}

.section {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(225px, 1fr));
  margin-top: 2.875rem;
}

.footer {
  text-align: center;
  font-size: 0.9375rem;
  color: var(--col-txt2);
}
.footer a {
  color: var(--facebook);
  margin-inline: 2px;
  font-weight: 700;
}
.footer a:hover {
  text-decoration: underline;
  text-underline-offset: 2px;
  text-decoration-thickness: 3px;
}

.toggle input[type=checkbox] {
  display: none;
}
.toggle__label {
  display: block;
  width: 50px;
  height: 24px;
  border-radius: 50px;
  cursor: pointer;
  position: relative;
  background: var(--toggle);
}
.toggle__label::after {
  position: absolute;
  content: "";
  width: 18px;
  height: 18px;
  right: 4px;
  top: 3px;
  border-radius: 50%;
  background-color: white;
  z-index: 1;
  transition: all 0.3s ease-in-out;
}
.toggle input:checked + label {
  background: linear-gradient(225deg, var(--toggle-bg-start) 0%, var(--toggle-bg-end) 98.2%);
}
.toggle input:checked + label::after {
  background-color: var(--bg-top-dark);
  right: calc(100% - 4px);
  transform: translateX(100%);
}
@media (prefers-color-scheme: dark) {
  .toggle :root {
    --color-mode: "dark";
    --col-bg: var(--bg-dark);
    --col-bg-top: var(--bg-top-dark);
    --col-bg-card: var(--bg-card-dark);
    --col-txt1: var(--dark-text1);
    --col-txt2: var(--dark-text2);
    --col-card-hover: var(--bg-card-hover-dark);
  }
}

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border-radius: 0.3125rem;
  padding: 1.5625rem;
  height: 13.75rem;
  background-color: var(--col-bg-card);
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.card:hover {
  background-color: var(--col-card-hover);
}
.card::after {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  height: 3px;
}
.card--facebook::after {
  background-color: var(--facebook);
}
.card--twitter::after {
  background-color: var(--twitter);
}
.card--instagram::after {
  background-image: linear-gradient(to right, var(--insta-start), var(--insta-end));
}
.card--youtube::after {
  background-color: var(--youtube);
}
.card__top {
  display: flex;
  align-items: center;
}
.card__username {
  margin-left: 0.5rem;
  color: var(--col-txt1);
  font-size: 0.75rem;
  font-weight: 700;
}
.card__middle {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.card__middle > p:last-child {
  color: var(--col-txt1);
  text-transform: uppercase;
  font-size: 0.75rem;
  letter-spacing: 0.3125rem;
}
.card__count {
  font-weight: 700;
  font-size: 3.5rem;
  letter-spacing: -0.125rem;
  color: var(--col-txt2);
}
.card__bottom {
  display: flex;
  font-size: 0.625rem;
  font-weight: 700;
  align-items: center;
}
.card__bottom--up {
  color: var(--limegreen);
}
.card__bottom--down {
  color: var(--brightred);
}
.card__arrow {
  margin-right: 5px;
}

.card-overview {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  padding: 1.5625rem;
  height: 8.125rem;
  background-color: var(--col-bg-card);
  border-radius: 0.3125rem;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.card-overview:hover {
  background-color: var(--col-card-hover);
}
.card-overview__top {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.card-overview__bottom {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
}
.card-overview__count {
  font-size: 1.875rem;
  font-weight: 700;
  color: var(--col-txt2);
}
.card-overview__change {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-weight: 700;
}
.card-overview__change--up {
  color: var(--limegreen);
}
.card-overview__change--down {
  color: var(--brightred);
}
.card-overview__arrow {
  margin-right: 5px;
}